<template>
  <v-container class="mt-4">
    <v-row>
      <v-col>
        <v-row>
          <v-file-input accept=".xlsx" v-model="file"></v-file-input>
        </v-row>
        <v-row>
          <v-btn
            class="mx-auto"
            color="primary"
            :disabled="file.length == 0"
            @click="readExcel"
            >Upload</v-btn
          >
        </v-row>
        <v-row>
          <v-btn
            :disabled="studentsList.ACC == null"
            color="success"
            class="mx-auto mt-4"
            @click="getSamples"
            >Randomize</v-btn
          >
        </v-row>
        <v-row>
          <v-btn
            :disabled="randomSamples.ACC == null"
            color="secondary"
            class="mx-auto mt-4"
            @click="exportSheet"
            >Export</v-btn
          >
        </v-row>
      </v-col>
      <v-col>
        <v-simple-table dense height="300px">
          <thead>
            <th>Division</th>
            <th>Goal</th>
          </thead>
          <tbody>
            <tr v-for="(division, key) in divisionGoals" :key="key">
              <td>{{ key }}</td>
              <td>
                <v-text-field
                  min="0"
                  :disabled="studentsList.ACC == null"
                  type="number"
                  v-model="divisionGoals[key]"
                ></v-text-field>
              </td>
            </tr>
          </tbody>
        </v-simple-table>
      </v-col>
    </v-row>
    <div v-if="showSamples">
      <v-row v-for="(samples, key) in randomSamples" :key="key">
        <v-data-table
          :headers="headers"
          :items="samples"
          :items-per-page="samples.length"
          dense
          class="fill-width"
        ></v-data-table>
      </v-row>
    </div>
  </v-container>
</template>

<script>
const Excel = require("exceljs");
import { saveAs } from "file-saver";

export default {
  name: "RandomSampler",
  computed: {},
  data() {
    return {
      showSamples: false,
      headers: [
        {
          text: "Code",
          value: "code",
        },
        {
          text: "Name",
          value: "fullName",
        },
        {
          text: "Nationality",
          value: "nationality",
        },
        {
          text: "Year Level",
          value: "yearLevel",
        },
        {
          text: "Gender",
          value: "gender",
        },
        {
          text: "Course",
          value: "course",
        },
        {
          text: "Semester",
          value: "semester",
        },
        {
          text: "School Year",
          value: "schoolYear",
        },
        {
          text: "Division Code",
          value: "divisionCode",
        },
        {
          text: "Division",
          value: "division",
        },
        {
          text: "Section",
          value: "section",
        },
      ],
      file: [],
      columns: [
        "Code",
        "Full Name",
        "Nationality",
        "Year Level",
        "Gender",
        "Course",
        "Semester",
        "School Year",
        "Division Code",
        "Division",
        "Section",
      ],
      divisionGoals: {
        ACC: 85,
        BA: 103,
        SS: 74,
        HUM: 23,
        NURS: 35,
        EDUC: 9,
        ENGG: 132,
        NS: 16,
        CS: 23,
      },
      studentsList: {
        ACC: null,
        BA: null,
        SS: null,
        HUM: null,
        NURS: null,
        EDUC: null,
        ENGG: null,
        NS: null,
        CS: null,
      },
      randomSamples: {
        ACC: null,
        BA: null,
        SS: null,
        HUM: null,
        NURS: null,
        EDUC: null,
        ENGG: null,
        NS: null,
        CS: null,
      },
    };
  },
  methods: {
    async readExcel() {
      const workbook = new Excel.Workbook();
      const reader = new FileReader();

      reader.readAsArrayBuffer(this.file);
      reader.onload = () => {
        const buffer = reader.result;
        workbook.xlsx.load(buffer).then((workbook) => {
          console.log(workbook, "workbook");

          for (var [key] of Object.entries(this.studentsList)) {
            const worksheet = workbook.getWorksheet(key);
            const mapped = worksheet
              .getRows(2, worksheet.rowCount - 1)
              .map((r) => {
                return {
                  code: r.values[1],
                  fullName: r.values[2],
                  nationality: r.values[3],
                  yearLevel: r.values[4],
                  gender: r.values[5],
                  course: r.values[6],
                  semester: r.values[7],
                  schoolYear: r.values[8],
                  divisionCode: r.values[9],
                  division: r.values[10],
                  section: r.values[11] || null,
                };
              });
            this.studentsList[key] = mapped;
          }

          // const master = this.workbook.getWorksheet(1);
          // console.log(
          //   master.getRows(2, 10).map((r) => {
          //     return {
          //       code: r.values[1],
          //       fullName: r.values[2],
          //       nationality: r.values[3],
          //       yearLevel: r.values[4],
          //       gender: r.values[5],
          //       course: r.values[6],
          //       semester: r.values[7],
          //       schoolYear: r.values[8],
          //       divisionCode: r.values[9],
          //       division: r.values[10],
          //       section: r.values[11] || null,
          //     };
          //   })
          // );
        });
      };
    },
    getSamples() {
      for (const [key] of Object.entries(this.studentsList)) {
        this.randomSamples[key] = this.sample(
          this.studentsList[key],
          this.divisionGoals[key]
        );
      }
      this.showSamples = true;
    },
    sample(population, k) {
      if (!Array.isArray(population))
        throw new TypeError("Population must be an array.");
      var n = population.length;
      if (k < 0 || k > n)
        throw new RangeError("Sample larger than population or is negative");

      var result = new Array(k);
      var setsize = 21; // size of a small set minus size of an empty list

      if (k > 5)
        setsize += Math.pow(4, Math.ceil(Math.log(k * 3) / Math.log(4)));

      if (n <= setsize) {
        // An n-length list is smaller than a k-length set
        var pool = population.slice();
        for (var i = 0; i < k; i++) {
          // invariant:  non-selected at [0,n-i)
          var j = (Math.random() * (n - i)) | 0;
          result[i] = pool[j];
          pool[j] = pool[n - i - 1]; // move non-selected item into vacancy
        }
      } else {
        var selected = new Set();
        for (i = 0; i < k; i++) {
          j = (Math.random() * n) | 0;
          while (selected.has(j)) {
            j = (Math.random() * n) | 0;
          }
          selected.add(j);
          result[i] = population[j];
        }
      }

      return result;
    },
    exportSheet() {
      const workbook = new Excel.Workbook();
      for (const [key] of Object.entries(this.studentsList)) {
        var sheet = workbook.addWorksheet(key);
        sheet.columns = [
          {
            header: "Code",
            key: "code",
            width: 10,
          },
          {
            header: "Name",
            key: "fullName",
            width: 20,
          },
          {
            header: "Nationality",
            key: "nationality",
            width: 10,
          },
          {
            header: "Year Level",
            key: "yearLevel",
            width: 10,
          },
          {
            header: "Gender",
            key: "gender",
            width: 10,
          },
          {
            header: "Course",
            key: "course",
            width: 10,
          },
          {
            header: "Semester",
            key: "semester",
            width: 10,
          },
          {
            header: "School Year",
            key: "schoolYear",
            width: 10,
          },
          {
            header: "Division Code",
            key: "divisionCode",
            width: 10,
          },
          {
            header: "Division",
            key: "division",
            width: 20,
          },
          {
            header: "Section",
            key: "section",
            width: 10,
          },
        ];
        this.randomSamples[key].forEach((row) => {
          sheet.addRow(row);
        });
      }
      workbook.xlsx.writeBuffer().then((data) => {
        var blob = new Blob([data], {
          type:
            "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
        });
        saveAs(blob, "randomSamples.xlsx");
      });
    },
  },
};
</script>
